
.tabtable{width:100%;height:100%
}
.tabtable .el-table__body-wrapper{overflow:hidden
}
.tabtable .el-table{background-color:#0E2D6A;color:#FFFFFF
}
.tabtable .el-table .el-table__row{background-color:#0E2D6A;color:#FFFFFF;text-align:center
}
.tabtable .el-table thead,.tabtable .el-table th,.tabtable .el-table th .cell{background-color:#0E2D6A;color:#FFFFFF;text-align:center
}
.tabtable .el-table tr{background-color:#0E2D6A
}
.tabtable .el-table__body tr td:nth-child(1){text-align:left
}
.tabtable .el-table__body tr:hover>td{background-color:#0E2D6A !important;background-clip:padding-box
}
.tabtable .name{font-size:20px;margin-top:10px;margin-left:30px;color:#fff;position:relative;cursor:pointer
}
.tabtable .sub-name{font-size:20px;margin-top:10px;margin-left:10px;color:#ABABAB;position:relative
}
.tabtable .sub-name.active{color:#FFFFFF
}
.tabtable .name:before{content:"";display:block;position:absolute;width:5px;height:16px;background-color:#32A4DD;left:-20px;top:1px
}
.tabtable .value{font-size:36px;margin-top:10px;color:#fff;float:right;margin-right:30px
}
.tabtable .unit{font-size:18px;font-weight:bolder;margin-left:5px
}



