
.mainbox{border-top:#23569A 3px solid;width:100%;height:100%
}
.mainbox .el-table{background-color:#0E2D6A;color:#FFFFFF
}
.mainbox .el-table .el-table__row{background-color:#0E2D6A;color:#FFFFFF
}
.mainbox .el-table thead,.mainbox .el-table th,.mainbox .el-table th .cell{background-color:#0E2D6A;color:#FFFFFF
}
.mainbox .el-table__body tr:hover>td{background-color:#0E2D6A !important;background-clip:padding-box
}
.mainbox .name{font-size:20px;margin-top:10px;margin-left:30px;width:250px;color:#fff;position:relative;cursor:pointer;z-index:9999
}
.mainbox .sub-name{font-size:20px;margin-top:10px;margin-left:10px;color:#ABABAB;position:relative
}
.sub-name.active{color:#FFFFFF
}
.mainbox .name:before{content:"";display:block;position:absolute;width:5px;height:16px;background-color:#32A4DD;left:-20px;top:1px
}
.mainbox .value{font-size:36px;margin-top:10px;color:#fff;float:right;margin-right:30px
}
.mainbox .unit{font-size:18px;font-weight:bolder;margin-left:5px
}
.sub-chart{height:320px;width:31%;margin-left:15px;margin-right:10px;padding-top:10px;float:left;margin-top:10px;background-color:rgba(14,53,114,0.5) !important
}



