
.myleft{
	width: 50%;
	height: 100%;
	float: left;
}
.myright{
	width: 50%;
	height: 100%;
	float: left;
}
.comtitle{
	width: 100%;
	height: 30px;
	color: #000000;
	font-weight: bold;
}
.blanks{
	width: 5px;
	height: 20px;
	background-color: #0eb8f3;
	float: left;
}
.blankname{
	width:300px;
	height: 20px;
	font-weight: bold;
	float: left;
	margin-left: 16px;
	line-height: 19px;
	font-size: 20px;
	color: #4a5f7e;
}
.infoPageModel{
	width: 100%;
	height: 100%;
}
.content{
	width: 100%;
	height: auto;
	margin-top: 20px;
}
.leftDiv{
		width: 100%;
		float: left;
		padding-bottom: 20px;
		box-shadow: 1px 1px 8px 2px #c0a0a0;
}
.rightDiv{
	width: 100%;
	float: left;
	left:20px;
	position: relative;
	font-size: 14px;
	color: #7b939d;
	line-height: 26px;
	padding-bottom: 20px;
}
.upDiv{
	height: 459px;
	box-shadow: 1px 1px 8px 2px #c0a0a0;
	padding-left:20px ;
	padding-right:20px ;
}
.upDiv>p{
		line-height: 50px !important;
		line-height: 14px;
}
.botDiv{
	height: 534px;
	margin-top: 20px;
	box-shadow: 1px 1px 8px 2px #c0a0a0;
	padding-left:20px ;
	padding-right:20px ;
}
.botDiv a{
	line-height:40px ;
	font-size: 16px;
}
.botDiv a:hover{
		cursor:pointer;
		border-bottom:1px solid #7b939d;
}
.rightTitle{
	height: 30px;
	padding: 10px;
	padding-top: 15px;
	padding-bottom: 3px;
	font-size:18px;
	font-weight: bold;
	color:rgb(74,95,126);
}
.rightTitle a:hover{
	cursor:pointer;
	border-bottom:1px solid rgb(32,160,255);
}
.newtime{
	float: right;
}
.row1{
			padding-top: 20px;
			width: 100%;
			height: 80px;
			font-size: 14px;
			color: #7b939d;
			line-height: 26px;
			padding-bottom: 20px;
			padding-left: 22px;
}
.row2{
			width: 100%;
			height: auto;
			margin-left: 0px;
			margin-top: 20px;
}
.mapimg{
			background: red;
			width: 100%;
			height: auto;
}
.mainInfo{
			width: 100%;
			height: auto;
}
.leftbottomdiv{
	margin-top: 20px;	
	padding-bottom: 8px;
}
input[name='but']{
	width: 92px;
	height: 30px;
	border-radius: 15px;
	float: right;
	margin-right: 20px;
	border:1px solid  rgb(117,212,240);
	color: rgb(117,212,240);
	text-align: center;
}
hr{
	color: #ccc;
}



