
.search-box{width:600px;height:200px;position:absolute;top:30%;left:50%;margin-left:-300px;margin-top:-100px;font-size:18px
}
.search-box .el-input__inner{height:45px;border:#20a0ff 1px solid;font-size:18px
}
.search-box .el-input-group__append{background:#20a0ff;color:#fff;border:#20a0ff 1px solid;width:70px;text-align:center;font-size:18px
}
.search-box .el-input-group__append .el-button{margin-left:5px
}
.search-box .logo{overflow:hidden;margin-left:180px;margin-bottom:10px
}
.search-box .logo .icon{width:178px;height:88px;background:url("../../../../static/img/application/.png") no-repeat
}
.search-box .logo .text{font-size:30px;float:left;margin-top:8px;color:#444;letter-spacing:3px
}
.search-box .logo .text.text-right{margin-left:6px
}
.search-box>label{position:absolute;right:-94px;top:111px
}
.search-box .type-list{margin-top:10px
}
.search-box .type-list li{float:left;margin-left:30px
}
.search-box .type-list li.active{color:#d4237a;text-decoration:underline
}
.search-box .type-list li:first-child{margin-left:5px
}



